import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class Input extends React.Component {

    constructor(props)
      {
        super(props);
        this.textChange = this.textChange.bind(this);
        this.state = {
          inText: '',
          };
      }

    textChange(event){
        this.setState({
               inText: event.target.value}
             , () => {
        this.props.onInText(this.state.inText)
      });
      }


  render() {
    return <div className='Input'>
              <input type={ this.props.type } name={ this.props.name } placeholder={ this.props.placeholder } required autoComplete='off'
                onChange={this.textChange.bind(this)}
                value={this.state.inText}/>
              <label htmlFor={ this.props.name } ></label>
           </div>
         }
       }


class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to React</h2>
        </div>
        <form onSubmit= { this.loginSubmit }>
                <Input type='text' name='Customer Name' placeholder='Customer Name'
                onInText={this.handleUText} />
                <Input type='text' name='Customer Number' placeholder='Customer Number'
                onInText={this.handleUPwd}/>
                <Input type='text' name='Customer City' placeholder='Customer City'
                onInText={this.handleUText} />
                <Input type='text' name='Customer ZipCode' placeholder='Customer ZipCode'
                onInText={this.handleUPwd}/>

                <button className="SignIn" > Sign In</button>
                <br />
                {
                      this.state.showstatusMs  ?
                          <label htmlFor= "ss">{localStorage.getItem('status')}</label> :
                          null
                }

              </form>

      </div>
    );
  }
}



export default App;



